@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
body {
  font-family: Roboto Mono;
  background-color: #2D3047;
  color: #FAF9F6;
}

button {
  font-family: inherit;
  text-align: center;
  font-weight: bold;
  border: none;
  cursor: pointer;
}

input, select, textarea {
    background: transparent;
    color: #FAF9F6;
    border: none;
    border-bottom: 1px solid #1B998B;
}

.addButton {
  border-radius: 5px;
  border: none;
  background-color: #FAF9F6;
}

.deleteButton {
  border-radius: 5px;
  border: none;
  background-color: #ED217C;
  color: white;
}

.error {
  color: red;
}

.notification {
  color: greenyellow;
}

